package com.example.redwood.emojisearch.compose

import androidx.compose.runtime.Composable
import app.cash.redwood.Modifier
import app.cash.redwood.RedwoodCodegenApi
import app.cash.redwood.compose.RedwoodComposeNode
import app.cash.redwood.compose.WidgetNode
import com.example.redwood.emojisearch.widget.EmojiSearchWidgetFactoryProvider
import com.example.redwood.emojisearch.widget.Image
import kotlin.OptIn
import kotlin.String
import kotlin.Unit

@Composable
@OptIn(RedwoodCodegenApi::class)
public fun Image(
  url: String,
  onClick: (() -> Unit)? = null,
  modifier: Modifier = Modifier,
) {
  RedwoodComposeNode<EmojiSearchWidgetFactoryProvider<*>, Image<*>>(
      factory = { it.EmojiSearch.Image() },
      update = {
        set(modifier, WidgetNode.SetModifiers)
        set(url) { recordChanged(); widget.url(it) }
        set(onClick) { recordChanged(); widget.onClick(it) }
      },
      content = {
      },
      )
}
