package com.example.redwood.emojisearch.widget

import app.cash.redwood.layout.widget.RedwoodLayoutWidgetFactory
import app.cash.redwood.layout.widget.RedwoodLayoutWidgetFactoryProvider
import app.cash.redwood.lazylayout.widget.RedwoodLazyLayoutWidgetFactory
import app.cash.redwood.lazylayout.widget.RedwoodLazyLayoutWidgetFactoryProvider
import app.cash.redwood.widget.Widget
import kotlin.Any
import kotlin.OptIn
import kotlin.experimental.ExperimentalObjCName
import kotlin.native.ObjCName

@OptIn(ExperimentalObjCName::class)
@ObjCName(
  "EmojiSearchWidgetFactories",
  exact = true,
)
public class EmojiSearchWidgetFactories<W : Any>(
  override val EmojiSearch: EmojiSearchWidgetFactory<W>,
  override val RedwoodLayout: RedwoodLayoutWidgetFactory<W>,
  override val RedwoodLazyLayout: RedwoodLazyLayoutWidgetFactory<W>,
) : EmojiSearchWidgetFactoryProvider<W>

public interface EmojiSearchWidgetFactoryProvider<W : Any> : Widget.Provider<W>,
    RedwoodLayoutWidgetFactoryProvider<W>, RedwoodLazyLayoutWidgetFactoryProvider<W> {
  public val EmojiSearch: EmojiSearchWidgetFactory<W>
}
